import React from 'react';
import axios from 'axios';
import Lotte from "../components/Lotte";
import Cgv from "../components/Cgv";
import '../components/MovieStyle.css';

class Home extends React.Component{

    state = {
        isLoading : true,
        movie : {}
    }
    
    getMovie = async ()=> {
        console.log(this.props)
        const data = await axios.get(this.props.location.urlLink);
       // const {data : {data}} = await axios.get(this.props.location.urlLink);
        //const {data : {data}} = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
        console.log(data)
        this.setState(current => ({movie : data , 
                                   isLoading : !current, 
                                   movieName : this.props.location.movieName,
                                   link : this.props.location.urlLink
                                }));

       // movie.data.status == 'ok' ? alert("success") : alert("not")
    }

    componentDidMount(){
        this.getMovie();
    }

    render(){
        const {isLoading, movie, movieName ,link} = this.state;
    
        return (
            <section className="container">
                {isLoading ? <div className="loader"><span className="loader__text">Loading...</span></div>                 
                : 
                <div className= "movies">
                
                { movieName == 'CGV' ? 
                movie.data.boxOfficeResult.weeklyBoxOfficeList.map(movie =>{
                 return   <Cgv key={movie.movieCd}
                        id ={movie.movieCd}
                        title = {movie.movieNm}
                        year = {movie.openDt}
                        rank = {movie.rank}
                        link = {link}
                        movieName = {movieName}
                        />
                }) :
                movie.data.data.movies.map(movie =>{
                    return   <Lotte key={movie.id}
                           id ={movie.id}
                           title = {movie.title}
                           year = {movie.year}
                           summary = {movie.summary}
                           genre = {movie.genres}
                           image = {movie.medium_cover_image}
                           link = {link}
                           movieName = {movieName}
                           />
                   })
                }
                </div>
                }
            </section>
        )
    }

}

export default Home;