import  React from "react";
import Link from "react-router-dom";
import './MovieStyle.css';


class MovieDetail extends React.Component{
    componentDidMount(){
        const {location, history, match} = this.props;
        console.log(location)
        console.log(history)
        if(location.state == undefined){
            history.push("/")
        }else{
            
        }
    }

    goBack  = (history) =>{
        /*console.log(history)
        //history.goBack();
        history.go(-1)*/
        history.push({
            pathname : "/home",
            urlLink : history.location.state.link,
            movieName : history.location.state.movieName
        })
    }

    render(){
        const {location, history, match} = this.props;
        
        if(location.state){
            return (
                <div>    
                    <div className="movie">
                        <div className="movie__poster"></div>                
                        <span>{location.state.title}</span>
                        <div><span onClick={ () => {this.goBack(history)}}>Go back</span></div>
                    </div>
                </div>
            )
        }else{
            return null;
        }
        
    }
}

