import  React from "react";
import Link from "react-router-dom";
import './MovieStyle.css';
import axios from 'axios'


class MovieDetail_cgv extends React.Component{

    state = {
        isLoading : true
    }
    componentDidMount(){
        const {location, history, match} = this.props;
        
        if(location.state == undefined){
            history.push("/")
        }else{
            
        }
        
        this.getDetail(match);
    }

    goBack  =  (history) =>{
        history.push({
            pathname : "/home",
            urlLink : history.location.state.link,
            movieName : history.location.state.movieName
        })
    }

    getDetail = async (match) =>{
        console.log(match)
        const {data :  {movieInfoResult : {movieInfo}}} = await axios.get("http://www.kobis.or.kr/kobisopenapi/webservice/rest/movie/searchMovieInfo.json?key=f5eef3421c602c6cb7ea224104795888&movieCd="+match.params.id);
        this.setState(current => ({ movieDetail : movieInfo, isLoading : false,}));

            console.log(this.state)
    }

    render(){
        const {location, history, match} = this.props;
        const {movieDetail,isLoading} = this.state;
       

        if(location.state){
            return (
                <section className="container">
                <div> <span onClick={ () => {this.goBack(history)}}>Go back</span>

                    { !isLoading ? 
                    <div className="movie">
                        <div className="movie__data">
                            <h3 className="movie__title">{movieDetail.movieNm} ({movieDetail.prdtYear})</h3>
                            <ul className="movie__genres">
                            <li>Genres ::</li>
                            {movieDetail.genres.map((genre,index) => {
                                return <li key={index} className="genres__genre" >{genre.genreNm}</li>
                            })}
                            </ul>
                            <ul className="movie__genres">
                            <li>Actors ::</li>
                            {movieDetail.actors.map((actor,index) => {
                                return <li key={index} className="genres__genre" >{actor.peopleNmEn}</li>
                            })}
                            </ul>
                            <ul className="movie__genres">
                                <li>Dirctors ::</li>
                            {movieDetail.directors.map((director,index) => {
                                return <li key={index} className="genres__genre" >{director.peopleNmEn}</li>
                            })}
                            </ul>
                        </div>
                    </div>:
                    <div className="loader"><span className="loader__text">Loading...</span></div>
                    }
                </div>
                </section>
            )
        }else{
            return null;
        }
        
        
    }
}

