import { render } from '@testing-library/react';
import React from 'react';
import styled from 'styled-components';
import './components/MovieStyle.css'
import { Link } from "react-router-dom";



const StyledButton = styled.button`
  background: transparent;
  border-radius: 5px;
  border-width: 7px;
  border-style: solid;
  border-color: palevioletred;
  color: palevioletred;
  padding: 0.25em 1em;
  font-size: 80px;
  cursor: pointer;
  width : 500px;
  height : 500px;
`;

const StyledButton1 = styled(StyledButton)`
border-color: cadetblue;
color: cadetblue;
margin-left: 20px;  
font-size: 65px;
`;

const Config = {
    cgvLink : "https://kobis.or.kr/kobisopenapi/webservice/rest/boxoffice/searchWeeklyBoxOfficeList.json?key=f5eef3421c602c6cb7ea224104795888&targetDt=20120101",
    lotteLink : "https://yts-proxy.now.sh/list_movies.json?sort_by=rating",
}

function Intro () {
    return(
        <div className='intro'>
           <Link to={
               {
                   pathname : `/home`,
                   urlLink : Config.cgvLink,
                   movieName : "CGV"
               }
           } className='link'><StyledButton>CGV</StyledButton></Link>
           <Link to={
               {
                   pathname : `/home`,
                   urlLink : Config.lotteLink,
                   movieName : "Movie"
               } 
            }className='link'><StyledButton1>롯데시네마</StyledButton1></Link>
        </div>
    )
}

export default Intro;