import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import './MovieStyle.css';


function Cgv ({id, title, year,rank , link,movieName}){
    return (
        <Link to={
            {
                pathname : `/movie/cgv/${id}`,
                state : {
                    id:id,
                    title:title,
                    year:year,
                    rank :rank,
                    link :link,
                    movieName
                }
            }
        }>
        <div className="movie"> 
            <div className="movie__data">
                <h3 className="movie__title">{title} ({year})</h3>
                <ul className="movie__genres">
                    <li key={id} className="genres__genre" >{rank}</li>
                </ul>
            </div>
        </div>
        </Link>
    )
}

Cgv.propTypes = {
  };

export default Cgv;